}

.phone-in-nav a strong {
    display: block;
    margin-left: 10px;
    font-size: 16px;
    font-style: italic;
}
.header {
    position: relative;
    width: 100%;
    height: 100%;
}

.header h1 {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.2);
    padding: 10px 0;
    z-index: 5;
    color: white;
    letter-spacing: 3px;
    text-align: center;
    font-size: 86px;
    font-family: 'Tangerine', cursive;
    text-shadow: 1px 1px 5px #000;
}
.header h1 strong {
    display: block;
    padding: 2px 0;
    font-weight: normal;
}

.slick-next,
.slick-prev {    
    z-index: 5;
    right: 5%;
}

.slick-prev {
    left: 5%;
}
.page-animations{
    width: 100%;
    background: var(--greenGradient);
    padding: 50px 0 100px 0;
}

.animations {
    width: var(--widthContent);
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.animations h2 {
    width: 100%;
    margin-bottom: 50px;
    font-weight: 900;
    font-size: 100px;
    color: white;
    font-family: 'Tangerine', cursive;  
}

.content-animation-box {
    width: 30%;
    margin-bottom: 20px;
    text-align: center;
    color: white;
}

.animation-img-wrapper {
    width: 250px;
    height: 250px;
    margin: 0 auto;
    background-image: url(/ARTInka/static/media/the-sun.8d859cce.png);
    background-repeat: no-repeat;
    background-size: 250px 250px;
    transition: -webkit-transform .5s linear;
    transition: transform .5s linear;
    transition: transform .5s linear, -webkit-transform .5s linear;
}

.animation-img-wrapper:hover {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

.animation-img-wrapper:hover div img {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

.animation-img-wrapper div {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .2s linear;
    cursor: pointer;
}

.animation-img-wrapper div img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    transition: -webkit-transform .5s linear;
    transition: transform .5s linear;
    transition: transform .5s linear, -webkit-transform .5s linear;
}

.content-animation-box h4 {
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 28px;
    letter-spacing: 2px;
    font-weight: 300;
    text-shadow: 1px 1px 1px #000;    
}

.content-animation-box p {
    line-height: 130%;
    font-size: 14px;
    letter-spacing: 1px;
    font-style: italic;
}
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: rgba(27, 27, 27, 0.541);
    display: flex;
    align-items: center;
  }

  .overlay img {
    display: block;
    max-width: 80%;
    max-height: 800px;
    margin: 60px auto;
    box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  }
  
  span.dismiss {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 30px;
    color: #ffffff;
    z-index: 10;
    cursor: pointer;
  }

  .overlay-arrows_left {
    display: flex;
    background-color: #6d6d6d;
    justify-content: space-between;
    position: absolute;
    top: 50%;
    left: 5%;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    z-index: 10;
    cursor: pointer;
  }
  
  .overlay-arrows_left svg,.overlay-arrows_right svg {
    padding: 5px;
    width: 30px;
    height: 30px;
    color: white;
  }
  
  .overlay-arrows_right {
    display: flex;
    background-color: #6d6d6d;
    justify-content: space-between;
    position: absolute;
    right: 5%;
    width: 30px;
    height: 30px;
    top: 50%;
    border-radius: 50%;
    z-index: 10;
    cursor: pointer;
  }
