.page-gallery {
    width: 100%;
    background-color: var(--red); 
}

.page-gallery h2 {
  width: var(--widthContent);
  margin: 0 auto;
  padding-top: 50px;
  color: white;
    font-weight: 900;
    font-size: 100px;
    font-family: 'Tangerine', cursive;
}

  .gallery {
    width: var(--widthContent);
    padding: 50px 0 100px 0;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
  }

  .wrapper-img {
    width: 20%;
    overflow: hidden;
  }
  
  .wrapper-img img {
    display: block;
    width: 100%;
    cursor: pointer;
    transition: transform .2s linear;
    
  }

  .wrapper-img img:hover {
    transform: scale(1.2);
  }

  .wrapper-img img.duze {
    display: none;
  }
  