.about-in-home-page {
    padding: 100px 0;
    background: var(--greenGradient);;
}

.wrapper-about-in-home-page {
    width: var(--widthContent);
    margin: 0 auto;

    display: flex;
    justify-content: space-between;
}

.content-about-in-home-page {
    width: 50%;
    padding: 50px 0;
    color: white;
}

.content-about-in-home-page h2 {
    margin-bottom: 25px;
    font-weight: 900;
    font-size: 100px;
    font-family: 'Tangerine', cursive;

}

.content-about-in-home-page p {
    line-height: 150%;
}

.content-about-in-home-page p span {
    display: block;
    width: 110%;
    margin-bottom: 5px;
    font-size: 18px;
    font-weight: 700;
}

/* PHOTOS */

.photos-about-in-home-page {
    position: relative;
    width: 50%;
}

.photos-about-in-home-page img {
    position: absolute;
    left: 40%;
    border-radius: 50%;
}

.photos-about-in-home-page img:nth-child(1){
    top: 180px;
    transform: translateX(190px);
    width: 240px;
    height: 240px;
    z-index: 3;
}
.photos-about-in-home-page img:nth-child(2){
    top: 0;
    width: 335px;
    height: 335px;
    z-index: 2;
}
.photos-about-in-home-page img:nth-child(3){
    top: 230px;
    transform: translateX(-120px);
    width: 205px;
    height: 205px;
}